#navbar{background:#79bac1;position:relative;display:flex;flex-wrap:wrap;align-items:center;justify-content:space-between;box-shadow:0px 0.1em 0.3em rgba(0,0,0,0.7)}#navbar svg{padding:0.5em;width:3em;height:3em}#navbar ul{display:flex;list-style:none}#navbar ul li{margin:1em;font-size:1.1em;text-transform:uppercase;font-weight:600;color:#f9f9f9;cursor:pointer}#navbar ul li:hover{opacity:90%}
